.percent_calc {
  position: relative;
  height: 82vh;
  display: flex;
  flex-direction: column;
}

.percent_calc_block {
  display: flex;
  align-items: center;

}

.percent_calc_blocks {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 50px;
  flex-wrap: wrap;
}

.percent_calc_img {
  width: 190px;
  height: auto;
}

.percent_calc_amount {
  font-size: 40px;
}

@media (max-width: 768px) {

  .reverse {
    flex-direction: row-reverse;
  }
}


@media (max-width: 425px) {
  .percent_calc_img {
    width: 120px;
    height: auto;
  }

  .percent_calc_amount {
    font-size: 30px;
  }

  .percent_calc_blocks {

    margin-bottom: 20px;

  }
}

@media (max-width: 320px) {
  .percent_calc_img {
    width: 120px;
    height: auto;
  }

  .percent_calc_amount {
    font-size: 20px;
  }
}